import React, { Component } from 'react'
import logo from './logo.svg';
/*import Search from "./Search.js"; */

export default class App extends Component {

  constructor(props){
    super(props);
      this.state = {
        search: ''
      };
  }

  updateSearch(event){
    this.setState({search: event.target.value});
  }

  componentDidMount(){
    const url = "https://jogavidia.github.io/ctperu-json/ctperu.json";

    fetch(url)
      .then(result => result.json())
      .then(result => {
        this.setState({
          data: result
        })
      });
    }

  render() {
    if (!this.state.data) return <p>Loading...</p>
    let filteredCity = this.state.data.cities.filter(
      (postDetail) => {
        return postDetail.cityName.toLowerCase().indexOf(this.state.search.toLowerCase()) !== -1 ||
         postDetail.place.toLowerCase().indexOf(this.state.search.toLowerCase()) !== -1;
      }
    );
    return(
      <div className="wrap">
        <div className="header">
          <img className="logo" src={logo} alt="Logo" />        
          <p>{this.state.data.about}</p>

        </div>
        <div className="search">
          <div className="input-wrap">
            <input type="text" value={this.state.search}
            onChange={this.updateSearch.bind(this)} placeholder="Buscar..."/>
          </div>
        </div>
        <div className="container">
        {
          filteredCity.map((postDetail) =>{
               
            return (
                           
              <div key={postDetail.id.toString()} className="info">
              <div className="callout">
                <h2>{postDetail.cityName}</h2>
              </div>
              <div className="callout">
                <p> <span>{postDetail.place}</span>{postDetail.description}</p>
              </div>  
                <div className="callout">
                  <div className="embajador-box">
                    <p>{postDetail.gender}<a href={"https://www.instagram.com/"+postDetail.embajador}>@{postDetail.embajador}</a></p>
                  </div>
                </div>
              </div>
              
            )
    
          })

        }
        </div>
      </div>
    )
  }
}
